import noData from '@/components/noData'
import back from '@/components/back'

export default {
    components:{
        noData,
        back
    },
    data(){
        return {
            page: 1,
            list: [],
            order: [],
            user: this.$store.state.userInfo,
            showConfirm: false,
            sendPackage: null,
            page: 1,
            haveMore: true,
            pageShow:false,
            typeList: []
        }
    },
    created(){
        this.$store.dispatch('pushservice', null)
        this.$store.dispatch('pushserviceCont',null)
        this.$store.dispatch('pushDisease', [])
        this.$store.dispatch('pushserviceBase', null)
        this.$store.dispatch('pushserviceFollowUp',null)
        this.pull(false)
        this.pullType()
    },
    mounted(){
        this.getMore()
    },
    beforeDestroy(){
        window.onscroll = null
    },
    methods:{
        pull(h){
            let Invalid = -1
            let PackStatus = -1
            if(!this.$route.query.fromService){
                Invalid = 0
                PackStatus = 1
            }
            this.$post('PlatFormAPI/ServicePackItem/QueryDrPackPageAndDetail',{
                DrId: this.user.Data.User.DoctorId,
                Invalid,
                PackStatus,
                PageIndex: this.page,
                PageSize: 15
            }).then(o => {
                this.pageShow = true
                if (!o.ReturnData || o.ReturnData.length < 15){
                        this.haveMore = false
                    }
                    else {
                        this.haveMore = true
                    }
                    
                    if (h){
                        o.ReturnData.forEach(v => {
                            this.list.push(v)
                        })
                    }
                    else {
                        this.list = o.ReturnData
                    }
            })
        },
        pullType(){
            this.$post('PlatFormAPI/ServicePackItem/QueryAllServiceClass').then(o => {
                this.typeList = o
            })
        },
        confirm(){
            const i = this.sendPackage
            const pack = {
                "Title": i.ServicePackName,
                "Content":[
                    {
                        "Type":"TextDetail",
                        "ContentElem":{
                            "Text":"服务总价",
                            "Detail": `${i.Price}元`
                        }
                    }
                ],
                "ExtData":{
                    "Type":32,
                    "Data":{
                        "serviceId": i.ServicePackId
                    }
                },
                "IsSystem":false
            }
            i.Details.forEach(v => {
                if(v.ValueType == 1){
                    pack.Content.push({
                        "Type":"TextDetail",
                        "ContentElem":{
                            "Text": v.ServiceItemName,
                            "Detail": `${v.NumValue}${v.NumTypeName}`
                        }
                    })
                }
                else if(v.ValueType == 4){
                    pack.Content.push({
                        "Type":"TextDetail",
                        "ContentElem":{
                            "Text": v.ServiceItemName,
                            "Detail": '按需'
                        }
                    })
                }
                
            })
            this.$bridge.callhandler('ServicePack', JSON.stringify(pack))
            this.$bridge.callhandler('Back')
        },
        toCreate(){
            if(this.$route.query.fromService){
                this.$store.dispatch('pushDisease', [])
                this.$store.dispatch('pushserviceBase', null)
                this.$store.dispatch('pushserviceCont', null)
                this.$router.push('/create-service-base')
            }
        },
        send(i){
            if(this.$route.query.fromService){
                this.$store.dispatch('pushDisease', [])
                this.$store.dispatch('pushserviceBase', null)
                if(i.PackStatus == 1){
                    return this.$router.push(`/package-detail?serviceId=${i.ServicePackId}`)
                }
                else{
                    this.$store.dispatch('pushservice', i)
                    return this.$router.push('/create-service-base')
                }
            }
            if(i.PackStatus == 1 && i.Invalid == 0){
                this.showConfirm = true
                this.sendPackage = i
            }
            else if(i.Invalid == 1){
                this.$toast('该服务包已下架')
            }
            else{
                this.$toast('该服务包尚未通过审核')
            }
        },
        getMore(){
            const el = this.$refs.el
            let timer = null
            window.onscroll = () => {
                clearTimeout(timer)
                timer = setTimeout(() => {
                    const top = document.documentElement.scrollTop
                                || window.pageYOffset
                                || document.body.scrollTop,
                            h = el.scrollHeight,
                            wh = window.innerHeight

                    if (h - top - wh < 100 && this.haveMore){
                        this.page += 1
                        this.pull(true)
                    }
                }, 100)
            }
        },
    }
}



